import React, { useEffect, useRef } from 'react';
import { Chart } from 'chart.js/auto';

const Speedometer = ({ speed }) => {
    const chartRef = useRef(null);

    useEffect(() => {
        const ctx = chartRef.current.getContext('2d');

        const speedometerChart = new Chart(ctx, {
            type: 'doughnut',
            data: {
                datasets: [
                    {
                        data: [speed, 100 - speed], // Adjust according to max speed
                        backgroundColor: ['#FF6384', '#E0E0E0'],
                        hoverBackgroundColor: ['#FF6384', '#E0E0E0'],
                    },
                ],
            },
            options: {
                rotation: 1 * Math.PI,
                circumference: 1 * Math.PI,
                cutout: '70%', // Adjust chart thickness
                plugins: {
                    tooltip: { enabled: false },
                    legend: { display: false },
                    title: {
                        display: true,
                        text: `${speed} km/h`,
                        font: { size: 24 },
                    },
                },
            },
        });

        // Cleanup
        return () => speedometerChart.destroy();
    }, [speed]);

    return <canvas ref={chartRef} width="400" height="400"></canvas>;
};

export default Speedometer;
